.label {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-bottom: 15px;

  color: #333232;
}

.input {
  width: 100%;
  padding: 11px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: 350px;
  }
}

.input:focus {
  outline: transparent;
  border: 1px solid #21f364;
}
